.transaction_history {
    display: table;
    width: 600px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 15px;
    box-shadow: 0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
}

.thead {
    background-color: rgb(48, 41, 114);
}

.th {
    text-transform: uppercase;
    font-size: 20px;

    padding: 10px;
    width: calc(100% / 3);
    color: #ffffff;
}

.tbody {
    color: rgb(16, 25, 143);
}

.tr {
    text-align: center;
}

.tr:nth-of-type(even) {
    background-color: rgb(240, 240, 240);
}

.td {
    padding: 15px;
    text-transform: capitalize;
}